*{
	/* border: 1px solid black; */
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
}

::-webkit-scrollbar {
	width: 8px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
	border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
	background: rgb(128, 128, 128); 
	border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
	background: rgb(100, 100, 100); 
  }

/* Header */
.flex-container{
  	display: flex;
	height: max-content;
	position: sticky;
	top: 0px;
	z-index: 5;
}

.flex-col{
	flex: 50%;
	padding: 40px;
}

.float-left{
	float: left;
}

.bullet-container{
	margin-left: 50px;
	padding-top: 50px;
	padding-bottom: 50px;
	width: 15px;
	margin-right: 10px;
}

.bullet{
	width: 15px;
	height: 15px;
	background-color: #0050ff;
}

.details-container{
	padding-top: 46px;
	padding-bottom: 46px;
	align-items: center;
	justify-content: center;
}

.inner-container{
	align-items: center;
	/* display: grid;
	grid-template-rows: 1fr;
	grid-template-columns: 100%; */
}

.basic-details-container{
	flex: 40%;
	background-color: white;
}

.nav-container{
	flex: 60%;
	padding: 30px;
}

.nav-container ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: white;
	top: 0;
}
  
.nav-container li {
	float: right;
}
  
.nav-container li a {
	display: block;
	color: black;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;
}
  
.nav-container li a:hover {
	background-color: white;
	color: #0050ff;
}

.nav-container li a:active{
	background-color: white !important;
}

ul.tablet-nav{
	display: none;
}

.active {
	display: block !important;
}

.activeLink{
	color: #0050ff !important;
}

.nav-toggle-btn{
	display: none;
}

.nav-toggle-btn span{
	display: block;
	width: 40px;
	height: 4px;
	transition: 0.5s;
	background-color: rgb(51, 51, 51);
	box-shadow: 0 2px 2px rgba(90, 90, 90, 0.2);
}

.nav-toggle-btn span:nth-child(1){
	transform: translateY(-5px);
	transition: 0.5s;
}

.nav-toggle-btn span:nth-child(4){
	transform: translateY(5px);
	transition: 0.5s;
}

.nav-toggle-btn span:nth-child(2){
	transform: translateY(2px);
	transition: 0.5s;
}

.nav-toggle-btn span:nth-child(3){
	transform: translateY(-2px);
	transition: 0.5s;
}

/* .nav-toggle-btn:hover span:nth-child(1){
	transform: translateY(-15px);
	opacity: 0;
}

.nav-toggle-btn:hover span:nth-child(4){
	transform: translateY(15px);
	opacity: 0;
} */

.nav-toggle-move-down{
	transform: translateY(15px) !important;
	opacity: 0;
}

.nav-toggle-move-up{
	transform: translateY(-15px) !important;
	opacity: 0;
}

.nav-toggle-rorate-ac{
	transform: translateY(2px) rotate(45deg) !important;
}

.nav-toggle-rotate-c{
	transform: translateY(-2px) rotate(-45deg) !important;
}

.bg-nav{
	background-color: bisque !important;
}

.nav-container{
	background-color: white;
	/* transition: 0.5s; */
}

.tablet-nav li:hover{
	background-color: rgb(255, 202, 138);
}

ul.desktop-nav{
	margin-left: 200px;
}

@media only screen and (max-width: 1275px){
	.nav-toggle-btn{
		display: block;
		position: absolute;
		right: 1rem;
		margin-top: 20px;
	}
	.basic-details-container{
		flex: 70%;
		background-color: white;
	}
	
	.nav-container{
		flex: 30%;
		padding: 30px;
	}
	.nav-container ul.desktop-nav{
		display: none;
	}

	.nav-container ul.tablet-nav{
		display: none; 
		background-color: bisque;
		
	}

	.nav-container li{
		float: none;
	}
}

.nav-tablet-opened{
	height: 100vh;
}

